import * as React from "react";
import type { HeadFC } from "gatsby";
import Layout from "../components/layout/Layout";
import { Link, navigate } from "gatsby";

import BSCard from "react-bootstrap/Card";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Figure from "react-bootstrap/Figure";
import Button from "react-bootstrap/Button";

import Item from "../page/Item";

import Parse from "../vendor/ParseApp";
import Table from "react-bootstrap/Table";
import { timeSince } from "../helpers/time";

const formatData = (e) => {
  if (Object.prototype.toString.call(e) === "[object Date]") {
    return timeSince(e);
  }

  if (typeof e === "string") {
    return e;
  }

  if (typeof e === "object") {
    return e.join(",");
  }
};

const Header = ({ data }) => {
  const dataModel = data.get("data");
  const jobTag = dataModel?.tags && dataModel?.tags.join(",");
  return (
    <div className="d-flex">
      {`Looking For ${jobTag}`}
      <div>{formatData(data.get("createdAt"))} ago</div>
    </div>
  );
};

const IndexPage = () => {
  const [items, setItems] = React.useState([]);

  React.useEffect(() => {
    Parse.Cloud.run("findMineItem").then((items) => {
      setItems(items);
    });
  }, []);

  return (
    <Layout>
      <Container className="">
        <h3>My Application</h3>
        <Row>
          <Col className="text-center" onClick={() => {}}>
            <Item items={items} cols={["title", "tags"]} Header={Header} />
          </Col>
        </Row>
      </Container>
    </Layout>
  );
};

export default IndexPage;

export const Head: HeadFC = () => <title>Home Page</title>;
